body {
  height: 100vh;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  position: fixed;
  top: 20px;
  left: 20px;
}

button[type="button"] {
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  transition: all .2s;
}

button {
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px;
  font-size: 16px;
}

.start-button {
  color: #fff;
  background-color: green;
}

.start-button:disabled {
  color: #fff;
  background-color: gray;
}

.stop-button {
  color: #fff;
  background-color: red;
}

.stop-button:disabled {
  color: #fff;
  background-color: gray;
}

